nav.App-nav {
  position: fixed;
  width: 100vw;
  height: 13vh;
  top: 0;
  left: 0;
  background-color: $dark;
  z-index: 1;
  color: $light;
  display: flex;
  align-items: center;
  justify-content: space-between;

  @keyframes shakePineapple {
    0% {
      transform: rotate(0);
    }
    15% {
      transform: rotate(5deg);
    }
    30% {
      transform: rotate(-5deg);
    }
    45% {
      transform: rotate(4deg);
    }
    60% {
      transform: rotate(-4deg);
    }
    75% {
      transform: rotate(2deg);
    }
    85% {
      transform: rotate(-2deg);
    }
    92% {
      transform: rotate(1deg);
    }
    100% {
      transform: rotate(0);
    }
  }
  img.App-nav-img {
    width: 3rem;

    object-fit: cover;
    margin-left: 2rem;

    &:hover {
      animation: shakePineapple 0.5s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
    }
  }
  ul.App-nav-ul {
    margin: 0;
    display: flex;
    list-style: none;
    justify-content: center;
    height: 100%;
    align-items: center;
    margin-right: 2rem;

    li {
      margin: 1rem;
      font-size: 1.3rem;
      text-transform: uppercase;
      cursor: pointer;
      transition: 0.2s;
      border-bottom: 0.01em solid transparent;

      &:hover {
        color: orange;
        transform: scale(1.2);
      }
    }
  }
}

@media (max-width: 509px) {
  nav.App-nav {
    display: none;
  }
}
